/*
PRIVACY
*/
#privacy .article__title, #deal .article__title {
  font-size: 26px;
  font-family: "PT Sans Narrow", sans-serif !important; }

#privacy .content .block__title, #deal .content .block__title {
  margin: 20px 0;
  padding-left: 20px;
  border-left: 2px solid;
  font-size: 16px;
  font-weight: bold; }

#privacy .content .block__conts .lists, #deal .content .block__conts .lists {
  margin: 20px 0 0 20px; }

#privacy .content .block__conts .list_title, #deal .content .block__conts .list_title {
  margin-top: 20px;
  display: list-item;
  list-style-type: decimal;
  font-size: 16px;
  font-weight: bold; }
  #privacy .content .block__conts .list_title:first-of-type, #deal .content .block__conts .list_title:first-of-type {
    margin-top: 0; }

#privacy .content .block__conts .list_content, #deal .content .block__conts .list_content {
  margin-top: 20px; }
  #privacy .content .block__conts .list_content h3, #deal .content .block__conts .list_content h3 {
    margin-top: 20px;
    font-size: 14px;
    font-weight: normal; }
  #privacy .content .block__conts .list_content ul li, #privacy .content .block__conts .list_content ol li, #deal .content .block__conts .list_content ul li, #deal .content .block__conts .list_content ol li {
    margin-left: 20px; }
  #privacy .content .block__conts .list_content aside, #deal .content .block__conts .list_content aside {
    margin: 20px 0;
    font-size: 12px; }

#privacy .content .block__conts .sign, #deal .content .block__conts .sign {
  margin-top: 20px;
  text-align: right; }

#privacy .content .block__conts table, #deal .content .block__conts table {
  width: 100%;
  border: 1px solid #e5e5e5; }
  #privacy .content .block__conts table thead, #deal .content .block__conts table thead {
    background: #f9f9f9; }
    #privacy .content .block__conts table thead tr, #deal .content .block__conts table thead tr {
      border-bottom: 1px solid #e5e5e5; }
      #privacy .content .block__conts table thead tr th, #deal .content .block__conts table thead tr th {
        border-left: 1px solid #e5e5e5;
        font-weight: normal; }
        #privacy .content .block__conts table thead tr th:first-of-type, #deal .content .block__conts table thead tr th:first-of-type {
          border-left: none; }
  #privacy .content .block__conts table tbody tr, #deal .content .block__conts table tbody tr {
    border-bottom: 1px solid #e5e5e5; }
    #privacy .content .block__conts table tbody tr th, #deal .content .block__conts table tbody tr th {
      font-weight: normal; }
    #privacy .content .block__conts table tbody tr td, #deal .content .block__conts table tbody tr td {
      text-align: center;
      border-left: 1px solid #e5e5e5; }

/*
CONTACT
*/
#contact .form {
  font-size: 16px;
  color: #000; }
  #contact .form .icon_kome {
    position: relative; }
    #contact .form .icon_kome::after {
      content: "\00203b";
      display: block;
      position: absolute;
      top: 10px;
      left: -20px;
      font-size: 16px;
      color: #9f2121; }
  #contact .form .box01 {
    padding: 20px 0;
    text-align: center;
    background: #e5e5e5; }
    #contact .form .box01__inr {
      width: 600px;
      margin: 20px auto 0;
      padding: 10px 0;
      background: #fff; }
      #contact .form .box01__inr label {
        padding: 0 50px; }
        #contact .form .box01__inr label input {
          margin-right: 10px; }
  #contact .form dl {
    margin-top: 50px; }
    #contact .form dl dt {
      float: left;
      width: 250px;
      line-height: 3; }
    #contact .form dl dd {
      float: right;
      width: 600px; }
      #contact .form dl dd input, #contact .form dl dd textarea {
        width: 100%;
        padding: 10px;
        line-height: 2;
        font-size: 16px;
        border: 1px solid #e5e5e5;
        box-sizing: border-box; }
      #contact .form dl dd label {
        display: block;
        margin-top: 40px; }
        #contact .form dl dd label input[type="checkbox"] {
          width: auto;
          margin-top: 10px;
          margin-right: 1em; }
      #contact .form dl dd textarea {
        height: 180px; }
      #contact .form dl dd .inr {
        width: 100%;
        height: 140px;
        padding: 10px;
        font-size: 12px;
        border: 1px solid #e5e5e5;
        box-sizing: border-box;
        overflow-y: scroll; }
        #contact .form dl dd .inr h2 {
          font-size: 12px;
          font-weight: normal; }
      #contact .form dl dd .aside {
        font-size: 12px; }
  #contact .form .link_next {
    margin-top: 40px; }
